import React, { Component } from "react";
import "./Inicio.css";
import continuar from "../assets/gif1.gif";
import { Redirect } from "react-router";
import UIfx from "uifx";
import mp3File from "../assets/beep.wav";

class Inicio extends Component {
  constructor(props) {
    super(props);
    this.state = {
      toGracias: false
    };
    this.rightFunction = this.rightFunction.bind(this);
    this.sleep = this.sleep.bind(this);
  }

 async rightFunction(event) {
    if (event.keyCode === 39) {
      await this.sleep(100);
      const beep = new UIfx(mp3File);
      beep.play();
      this.setState(() => ({
        toGracias: true
      }));
    }
  }

   sleep(milliseconds) {
    const date = Date.now();
    let currentDate = null;
    do {
      currentDate = Date.now();
    } while (currentDate - date < milliseconds);
  }

  componentDidMount() {
    document.addEventListener("keyup", this.rightFunction, false);
  }
  componentWillUnmount() {
    document.removeEventListener("keyup", this.rightFunction, false);
  }
  render() {
    if (this.state.toGracias === true) {
      return <Redirect to="/Gracias" />;
    }

    return (
      <div className="Inicio">
        <p>Aydanos a </p>
        <p>generar</p>
        <p>estadsticas</p>
        <img src={continuar} alt="continuar..." className="Inicio-img" />
      </div>
    );
  }
}

export default Inicio;
