import React, { Component } from "react";
import "./P21.css";
import continuar from "../assets/gif0.gif";
import seleccionar from "../assets/Seleccionar.png";
import square from "../assets/square.PNG";
import squareFull from "../assets/square-full.png";
import step3 from "../assets/step3.png";
import { Redirect } from "react-router";
import UIfx from "uifx";
import mp3File from '../assets/beep.wav';

class P21 extends Component {
  constructor(props) {
    super(props);
    this.state = {
      P1: this.props.location.pathname.slice(5,6),
      toP31: false,
      toP32: false,
      isSelected: false,
      isSelectedQ: [false, false, false, false],
      questionsHover: [true, false, false, false],
      actualHover: 0
    };
    this.rightFunction = this.rightFunction.bind(this);
    this.downFunction = this.downFunction.bind(this);
    this.upFunction = this.upFunction.bind(this);
    this.sleep = this.sleep.bind(this);
    console.log("this.props.location.pathname.slice(5,6)", this.props.location.pathname.slice(5,6))
  }

  async rightFunction(event) {
    if (event.keyCode === 39) {
      await this.sleep(100);
      console.log("Right arrow", this.state.isSelected);
      if (this.state.isSelected === true) {
        const beep = new UIfx(mp3File);
        beep.play();
        console.log("Right arrow plus selected");
        console.log(this.props.location.pathname.slice(5,6))
        console.log("this.state.toP31",this.state.toP31);
        if (
          this.state.P1 === '0' ||
          this.state.P1 === '2'
        ) {
          this.setState(() => ({
            toP31: true
          }));
        } else if (
          this.state.P1 === '1' ||
          this.state.P1 === '3'
        ) {
          this.setState(() => ({
            toP32: true
          }));
        }
      }
      let updIsSelectedQ = this.state.isSelectedQ;
      updIsSelectedQ[this.state.actualHover] = true;
      this.setState(() => ({
        isSelected: true,
        isSelectedQ: updIsSelectedQ
      }));
    }
  }

  downFunction(event) {
    if (event.keyCode === 40) {
      if (this.state.actualHover < 3) {
        this.setState(() => ({
          actualHover: this.state.actualHover + 1,
          isSelected: false,
          isSelectedQ: [false, false, false, false]
        }));

        let updQuestionsHover = this.state.questionsHover;
        updQuestionsHover[this.state.actualHover - 1] = false;
        updQuestionsHover[this.state.actualHover] = true;
        console.log("updQuestionsHover", updQuestionsHover);
        this.setState(() => ({
          questionsHover: updQuestionsHover
        }));
      }
    }
  }

  upFunction(event) {
    if (event.keyCode === 38) {
      if (this.state.actualHover > 0) {
        this.setState(() => ({
          actualHover: this.state.actualHover - 1,
          isSelected: false,
          isSelectedQ: [false, false, false, false]
        }));
        console.log("Actual", this.state.actualHover);
        let updQuestionsHover = this.state.questionsHover;
        updQuestionsHover[this.state.actualHover + 1] = false;
        updQuestionsHover[this.state.actualHover] = true;
        console.log("updQuestionsHover", updQuestionsHover);
        this.setState(() => ({
          questionsHover: updQuestionsHover
        }));
      }
    }
  }

  sleep(milliseconds) {
    const date = Date.now();
    let currentDate = null;
    do {
      currentDate = Date.now();
    } while (currentDate - date < milliseconds);
  }

  componentDidMount() {
    document.addEventListener("keyup", this.rightFunction, false);
    document.addEventListener("keyup", this.downFunction, false);
    document.addEventListener("keyup", this.upFunction, false);
  }
  componentWillUnmount() {
    document.removeEventListener("keyup", this.rightFunction, false);
    document.removeEventListener("keyup", this.downFunction, false);
    document.removeEventListener("keyup", this.upFunction, false);
  }
  render() {
    /* console.log(this.props.location.pathname.slice(4)) */
    if (this.state.toP31 === true) {
      return (
        <Redirect
          to={
            "/P31/" +
            this.state.P1 +
            "," +
            this.state.actualHover
          }
        />
      );
    }
    if (this.state.toP32 === true) {
      return (
        <Redirect
          to={
            "/P32/" +
            this.state.P1 +
            "," +
            this.state.actualHover
          }
        />
      );
    }
    return (
      <div className="P21">
        <div className="P21-text">
          <img src={step3} alt="Paso 2" className="P21-step" />
          <h1 className="P21-h1">Para ti <label className="P21-bigger">Qu es un piropo?</label></h1>

          <div className="P21-questions-container">
            <div
              className={
                this.state.questionsHover[0]
                  ? "P21-question-hover"
                  : "P21-question"
              }
            >
              <img
                src={this.state.isSelectedQ[0] ? squareFull : square}
                alt=""
              />
              <label className="P21-label">Un halago</label>
            </div>

            <div
              className={
                this.state.questionsHover[1]
                  ? "P21-question-hover"
                  : "P21-question"
              }
            >
              <img
                src={this.state.isSelectedQ[1] ? squareFull : square}
                alt=""
              />
              <label className="P21-label">Coqueteo</label>
            </div>

            <div
              className={
                this.state.questionsHover[2]
                  ? "P21-question-hover"
                  : "P21-question"
              }
            >
              <img
                src={this.state.isSelectedQ[2] ? squareFull : square}
                alt=""
              />
              <label className="P21-label">Una falta de respeto</label>
            </div>

            <div
              className={
                this.state.questionsHover[3]
                  ? "P21-question-hover"
                  : "P21-question"
              }
            >
              <img
                src={this.state.isSelectedQ[3] ? squareFull : square}
                alt=""
              />
              <label className="P21-label">Una agresin</label>
            </div>

          </div>
        </div>

        {this.state.isSelected ? (
          <img src={continuar} alt="continuar..." className="P21-img" />
        ) : (
          <img src={seleccionar} alt="continuar..." className="P21-imgS" />
        )}
      </div>
    );
  }
}

export default P21;
