import React, { Component } from "react";
import { HashRouter,  Route, withRouter } from "react-router-dom";
import Inicio from "./Inicio";
import Gracias from "./Gracias";
import P1 from "./P1";
import P2 from "./P2";
import P31 from "./P31";
import P32 from "./P32";
import P41 from "./P41";
import P43 from "./P43";
import P42 from "./P42";
import Final from "./Final";
import P21 from "./P21";
import P51 from "./P51";
import Final2 from "./Final2";



class Main extends Component {
  render() {
    return (
      <HashRouter basename="/centinela">
        <div>
          <Route exact path="/" component={Inicio} />
          <Route exact path="/Inicio" component={Inicio} />
          <Route path="/Gracias" component={Gracias} />
          <Route path="/P1" component={P1} />
          <Route path="/P2" component={P2} />
          <Route path="/P21" component={P21} />
          <Route path="/P31" component={P31} />
          <Route path="/P32" component={P32} />
          <Route path="/P41" component={P41} />
          <Route path="/P43" component={P43} />
          <Route path="/P42" component={P42} />
          <Route path="/P51" component={P51} />
      
          <Route path="/Final" component={Final} />
          <Route path="/Final2" component={Final2} />
        </div>
      </HashRouter>
    );
  }
}

export default withRouter(Main);
